import React, { useEffect } from 'react'
import CandidateProfileHighSchoolElementConnector from 'Employer/connectors/Components/CandidateProfileHighSchoolElementConnector'
import CandidateProfileUniversityElementConnector from 'Employer/connectors/Components/CandidateProfileUniversityElementConnector'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import cx from 'classnames'
import { map } from 'ramda'

function CandidateProfileEducation (props) {
  useEffect(() => {
    props.fetchEducationResources()
  }, [])

  const renderUniversityElement = education => <CandidateProfileUniversityElementConnector id={education.id} key={education.id} candidateId={props.candidateId} withHighlights={props.withHighlights} />
  const renderHighSchoolElement = highSchool => <CandidateProfileHighSchoolElementConnector id={highSchool.id} key={highSchool.id} />

  const UniversitiesList = () => map(
    renderUniversityElement
  )(props.universities)

  const HighSchoolsList = () => map(
    renderHighSchoolElement
  )(props.highSchools)

  const getList = () => props.isHighSchoolLevelEducation ? HighSchoolsList() : UniversitiesList()

  const containerClass = cx({
    'columns': !props.isMobile
  })

  return (
    <div className={containerClass}>{getList()}</div>
  )
}

export default withDeviceWidthDetector(CandidateProfileEducation)



