import React from 'react'
import CandidateProfileHeaderMobile from './CandidateProfileHeader.mobile'
import CandidateProfileHeaderWeb from './CandidateProfileHeader'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const CandidateProfileHeader = props => props.isMobile ? <CandidateProfileHeaderMobile {...props} /> : <CandidateProfileHeaderWeb {...props} />

export default withDeviceWidthDetector(CandidateProfileHeader)



