import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import CandidateProfileExperience from 'Employer/components/CandidateProfileDetails/CandidateProfileExperience'
import { getCandidateVolunteering } from 'Shared/selectors/experience'
import { getIdFromUrl } from 'Shared/helpers/string'

const candidateId = getIdFromUrl(window.location.pathname)

const mapStateToProps = state => ({
  experiences: getCandidateVolunteering(state, candidateId),
  withHighlights: false,
  candidateId
})

export default compose(
  withRouter,
  connect(mapStateToProps)
)(CandidateProfileExperience)



