import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import CandidateProfileNavbar from 'Employer/components/CandidateProfileDetails/CandidateProfileNavbar'
import { getCandidateDetails, getCandidateFullName } from 'Shared/selectors/candidates'
import { getIdFromUrl } from 'Shared/helpers/string'
import { getCandidateProfileNavigationItems } from 'Shared/helpers/profile'

const candidateId = getIdFromUrl(window.location.pathname)

const mapStateToProps = state => ({
  fullName: getCandidateFullName(state, candidateId),
  details: getCandidateDetails(state, candidateId),
  navigationItems: getCandidateProfileNavigationItems()
})

export default compose(
  withRouter,
  connect(mapStateToProps)
)(CandidateProfileNavbar)



