import React, { PureComponent } from 'react'
import { t } from 'i18next'
import { Helmet } from 'react-helmet'
import { Element } from 'react-scroll'
import { ProfilePreviewNavigationIds } from 'Candidate/constants/ProfilePreviewNavigation'
import Container from 'Shared/components/Container'
import CandidatePublicProfileHeaderConnector from 'Shared/connectors/CandidatePublicProfile/CandidatePublicProfileHeaderConnector.js'
import withTranslations from 'Shared/hoc/withTranslations'
import { candidatePublicProfile } from 'Shared/constants/MetaTags'
import { CandidateProfileViewLoader } from 'Shared/components/Main/MainLoaders'
import { cleanseString, capitalizeWords } from 'Shared/helpers/string'
import { addLightBackground, removeLightBackground } from 'Shared/helpers/layout'
import CandidatePublicProfileNavbarConnector from 'Shared/connectors/CandidatePublicProfile/CandidatePublicProfileNavbarConnector'
import ProfilePreviewSection from 'Candidate/components/ProfilePreviewSection'
import CandidatePublicProfileAboutConnector from 'Shared/connectors/CandidatePublicProfile/CandidatePublicProfileAboutConnector'
import CandidatePublicProfileEducationConnector from 'Shared/connectors/CandidatePublicProfile/CandidatePublicProfileEducationConnector'
import CandidatePublicProfileCertificatesConnector from 'Shared/connectors/CandidatePublicProfile/CandidatePublicProfileCertificatesConnector'
import CandidatePublicProfileExperienceConnector from 'Shared/connectors/CandidatePublicProfile/CandidatePublicProfileExperienceConnector'
import CandidatePublicProfileVolunteeringConnector from 'Shared/connectors/CandidatePublicProfile/CandidatePublicProfileVolunteeringConnector'
import CandidatePublicProfileSkillsConnector from 'Shared/connectors/CandidatePublicProfile/CandidatePublicProfileSkillsConnector'
import CandidatePublicProfileInterestsConnector from 'Shared/connectors/CandidatePublicProfile/CandidatePublicProfileInterestsConnector'
import CandidatePublicProfileLanguagesConnector from 'Shared/connectors/CandidatePublicProfile/CandidatePublicProfileLanguagesConnector'
import './CandidatePublicProfileView.scss'

class CandidatePublicProfileView extends PureComponent {
  state = {
    ready: false
  }

  componentWillMount () {
    if (this.props.isEmployer) {
      this.props.history.push(`/employer/candidate/${this.props.userId}`)
    }
  }

  async componentDidMount () {
    addLightBackground()
    this.props.fetchCountries()
    await this.props.fetchCandidatePublicProfile()
    this.setState({ ready: true })
  }

  componentWillUnmount () {
    removeLightBackground()
  }

  get pageTitle () {
    const { lastEducationName, firstName, lastName } = this.props
    return `${capitalizeWords(firstName)} ${capitalizeWords(lastName)}'s — ${lastEducationName} | Oliv`
  }

  get pageDescription () {
    const { t, lastEducationName, firstName } = this.props
    const transformedName = firstName.endsWith('s') ? `${capitalizeWords(firstName)}'` : `${capitalizeWords(firstName)}'s`

    return cleanseString(t('meta.candidatePublicProfile.description', { firstName: transformedName, universityName: lastEducationName }))
  }

  get profilePage () {
    const {
      hasCandidateAnyCertificates,
      hasCandidateAnyExperience,
      hasCandidateAnyVolunteering,
      hasCandidateAnySkills,
      hasCandidateAnyLanguages,
      hasCandidateAnyInterests
    } = this.props

    return (
      <section className='candidate-public-profile__container parsing--hidden'>
        <Helmet>
          <title>{this.pageTitle}</title>
          <meta name='description' content={this.pageDescription} />
          <meta property='og:title' content={this.pageTitle} />
          <meta property='og:type' content='website' />
          <meta property='og:image' content={candidatePublicProfile.image} />
          <meta property='og:image:width' content='200' />
          <meta property='og:image:height' content='200' />
          <meta property='og:url' content={window.location.href} />
          <meta property='og:description' content={this.pageDescription} />
        </Helmet>
        <Container>
          <CandidatePublicProfileHeaderConnector />
        </Container>
        <CandidatePublicProfileNavbarConnector />
        <Container>
          <Element name={ProfilePreviewNavigationIds.about}>
            <ProfilePreviewSection isWrapped header={t('common.about')}>
              <CandidatePublicProfileAboutConnector />
            </ProfilePreviewSection>
          </Element>
          <Element name={ProfilePreviewNavigationIds.education}>
            <ProfilePreviewSection isWrapped header={t('profile.education.title')}>
              <CandidatePublicProfileEducationConnector />
            </ProfilePreviewSection>
            {
              hasCandidateAnyCertificates && (
                <ProfilePreviewSection isWrapped header={t('profile.education.sections.additionalCertifications.label')}>
                  <CandidatePublicProfileCertificatesConnector />
                </ProfilePreviewSection>
              )
            }
          </Element>
          <Element name={ProfilePreviewNavigationIds.experience}>
            {
              hasCandidateAnyExperience && (
                <ProfilePreviewSection isWrapped header={t('profile.experience.title')}>
                  <CandidatePublicProfileExperienceConnector />
                </ProfilePreviewSection>
              )
            }
            {
              hasCandidateAnyVolunteering && (
                <ProfilePreviewSection isWrapped header={t('profile.experience.sections.volunteering.label')}>
                  <CandidatePublicProfileVolunteeringConnector />
                </ProfilePreviewSection>
              )
            }
            {
              hasCandidateAnySkills && (
                <ProfilePreviewSection isWrapped header={t('profile.experience.sections.skills.label')}>
                  <CandidatePublicProfileSkillsConnector />
                </ProfilePreviewSection>
              )
            }
          </Element>
          {
            hasCandidateAnyLanguages && (
              <Element name={ProfilePreviewNavigationIds.languages}>
                <ProfilePreviewSection isWrapped header={t('profile.additional.sections.languages.label')}>
                  <CandidatePublicProfileLanguagesConnector />
                </ProfilePreviewSection>
              </Element>
            )
          }
          {
            hasCandidateAnyInterests && (
              <Element name={ProfilePreviewNavigationIds.interests}>
                <ProfilePreviewSection header={t('profile.experience.sections.interests.label')}>
                  <CandidatePublicProfileInterestsConnector />
                </ProfilePreviewSection>
              </Element>
            )
          }
        </Container>
      </section>
    )
  }

  render () {
    return this.state.ready ? this.profilePage : <CandidateProfileViewLoader />
  }
}

CandidatePublicProfileView.defaultProps = {
  candidate: {
    firstName: ''
  }
}

export default withTranslations(CandidatePublicProfileView)



