import * as R from 'ramda'

export const parseHighlightedCandidateHit = hit => {
  const getElementParsedProp = (prop, value) => R.pipe(
    R.propOr([], prop),
    R.map(
      R.propOr('', value)
    )
  )(hit)

  const getElementParsedPropHighlight = (prop, value) => R.pipe(
    R.pathOr([], ['_highlightResult', prop]),
    R.map(
      R.propOr({ value: '' }, value)
    )
  )(hit)

  const parseNestedHighlights = ({
    experiencesDescription: getElementParsedProp('experiences', 'description'),
    skillsName: getElementParsedProp('skills', 'name'),
    majorsName: getElementParsedProp('majors', 'name'),
    certificatesName: getElementParsedProp('certificates', 'name'),
    experiencesPosition: getElementParsedProp('experiences', 'position'),
    experiencesOrganisationName: getElementParsedProp('experiences', 'organisationName'),
    certificatesIssuer: getElementParsedProp('certificates', 'issuer'),
    _highlightResult: {
      experiencesDescription: getElementParsedPropHighlight('experiences', 'description'),
      skillsName: getElementParsedPropHighlight('skills', 'name'),
      majorsName: getElementParsedPropHighlight('majors', 'name'),
      certificatesName: getElementParsedPropHighlight('certificates', 'name'),
      experiencesPosition: getElementParsedPropHighlight('experiences', 'position'),
      experiencesOrganisationName: getElementParsedPropHighlight('experiences', 'organisationName'),
      certificatesIssuer: getElementParsedPropHighlight('certificates', 'issuer')
    }
  })

  return R.mergeDeepRight(hit, parseNestedHighlights)
}



