import React from 'react'
import { map } from 'ramda'
import CandidateProfileLanguageElementConnector from 'Employer/connectors/Components/CandidateProfileLanguageElementConnector'

function CandidateProfileLanguages (props) {
  const renderLanguageElement = language => <CandidateProfileLanguageElementConnector language={language} key={language.languageCode} />

  const languagesList = map(
    renderLanguageElement
  )(props.languages)

  return (
    <div>{languagesList}</div>
  )
}

export default CandidateProfileLanguages



