import React from 'react'
import { t } from 'i18next'
import { isNotNilOrEmpty } from 'Shared/helpers/Ramda'
import Tooltip from 'Shared/components/Tooltip/Tooltip'
import HighlightedHitAttribute from 'Algolia/components/HighlightedHitAttribute'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import cx from 'classnames'
import './CandidateProfileUniversityElement.scss'

function CandidateProfileUniversityElement (props) {
  const getTitle = () => props.withHighlights ? (
    <HighlightedHitAttribute
      highlightWithOwnValue
      arrayItemAttribute='universities'
      arrayItemValue={props.title}
      array
      candidateId={props.candidateId}
      attribute='universities'
      value={props.title}
    />
  ) : props.title

  const containerClass = cx({
    'candidate-profile-university-element': true,
    'column col-6': !props.isMobile
  })

  return (
    <div className={containerClass}>
      <div className='candidate-profile-university-element-wrapper'>
        <div>
          <div className='candidate-profile-university-element__logo'>
            {
              isNotNilOrEmpty(props.logoUrl) ? (
                <img className='candidate-profile-university-element__logo-img' src={props.logoUrl} />
              ) : (
                <div className='candidate-profile-university-element__logo-icon icon-im-universities' />
              )
            }
          </div>
        </div>
        <div>
          <div className='candidate-profile-university-element__header'>{getTitle()}</div>
          <div className='candidate-profile-university-element__text'>{props.description}</div>
          <div className='candidate-profile-university-element__text candidate-profile-university-element__text--mobile-grey'>{props.graduationDate}</div>
          {props.gradingInfo && <div className='candidate-profile-university-element__text candidate-profile-university-element__text--mobile-grey'>{props.gradingInfo}</div>}
          {props.normalizedGrade && <div className='candidate-profile-university-element__text candidate-profile-university-element__text--mobile-grey'>
            {props.normalizedGrade}&nbsp;
            <Tooltip content={t('profile.education.normalizedGrade.description')}>
              <i className='ion-md-information-circle' />
            </Tooltip>
          </div>}
        </div>
      </div>
    </div>
  )
}

export default withDeviceWidthDetector(CandidateProfileUniversityElement)



