import React, { useEffect } from 'react'
import cx from 'classnames'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import './CandidateProfileLanguageElement.scss'
import { t } from 'i18next'
import { degree } from 'Shared/constants/Languages'
import { JustStarRating } from 'Shared/components/FormElements/RatingStars'

function CandidateProfileLanguageElement (props) {
  useEffect(() => {
    props.fetchLanguagesIfNeeded()
  }, [])

  const columnsClass = cx({
    'columns': !props.isMobile
  })

  const columnClass = cx({
    'column col-4': !props.isMobile
  })

  return (
    <div className='candidate-profile-language-element__wrapper'>
      <h4 className='candidate-profile-language-element__name'>{props.languageName}</h4>
      <div className={columnsClass}>
        <div className={columnClass}>
          <JustStarRating
            label={t('profile.additional.sections.languages.skill.speaking')}
            starCount={3}
            value={props.language.speaking}
            degreeDescription={degree()}
            quiet
            disabled
          />
        </div>
        <div className={columnClass}>
          <JustStarRating
            label={t('profile.additional.sections.languages.skill.writing')}
            starCount={3}
            value={props.language.writing}
            degreeDescription={degree()}
            quiet
            disabled
          />
        </div>
        <div className={columnClass}>
          <JustStarRating
            label={t('profile.additional.sections.languages.skill.reading')}
            starCount={3}
            value={props.language.reading}
            degreeDescription={degree()}
            quiet
            disabled
          />
        </div>
      </div>
    </div>
  )
}

export default withDeviceWidthDetector(CandidateProfileLanguageElement)



