import { connect } from 'react-redux'
import CandidateProfileLanguageElement from 'Employer/components/CandidateProfileDetails/CandidateProfileLanguages/components/CandidateProfileLanguageElement'
import { fetchLanguagesIfNeeded } from 'Shared/ducks/resources'
import { getLanguageByCode } from 'Shared/selectors/languages'

const mapStateToProps = (state, ownProps) => ({
  languageName: getLanguageByCode(state, ownProps.language.languageCode)
})

const mapDispatchToProps = dispatch => ({
  fetchLanguagesIfNeeded: () => dispatch(fetchLanguagesIfNeeded())
})

export default connect(mapStateToProps, mapDispatchToProps)(CandidateProfileLanguageElement)



