import React, { Fragment } from 'react'
import UserAvatar from 'Shared/components/UserAvatar/UserAvatar'
import HighlightedHitAttribute from 'Algolia/components/HighlightedHitAttribute'
import './CandidateProfileHeader.scss'
import blurredName from 'Shared/assets/images/other/blurred-name.png'
import PremiumTag from 'Shared/components/PremiumTag'

export const getFullName = (props) => {
  switch (true) {
    case !props.hasDatabaseSearchAvailable:
      return (
        <Fragment>
          <img className='candidate-profile-header__blurred-name' src={blurredName} />
        </Fragment>
      )
    case props.withAlgoliaHighlights:
      return (
        <Fragment>
          <HighlightedHitAttribute candidateId={props.id} attribute='firstName' value={props.firstName} />&nbsp;
          <HighlightedHitAttribute candidateId={props.id} attribute='lastName' value={props.lastName} />
        </Fragment>
      )
    default:
      return (
        <Fragment>
          <span>{props.firstName}</span>&nbsp;
          <span>{props.lastName}</span>
        </Fragment>
      )
  }
}

export const getMajor = props => props.withAlgoliaHighlights ? (
  <HighlightedHitAttribute array candidateId={props.id} attribute='majorsName' value={props.major} />
) : (
  <span>{props.major}</span>
)

function CandidateProfileHeader (props) {
  return (
    <div className='candidate-profile-header'>
      <div className='candidate-profile-header__introduction-wrapper'>
        <UserAvatar details={props.details} className='candidate-profile-header__photo' />
        <div className='candidate-profile-header__introduction'>
          <div>
            {
              props.hasOlivPlus && <div className='candidate-profile-header__tag'><PremiumTag /></div>
            }
            <div className='candidate-profile-header__heading'>
              {getFullName(props)}
            </div>
            <div className='candidate-profile-header__text candidate-profile-header__text--primary'>
              {getMajor(props)}
            </div>
            <div className='candidate-profile-header__text candidate-profile-header__text--capitalize candidate-profile-header__text--margin'>{props.location}</div>
            {props.contactSection()}
          </div>
        </div>
      </div>
      <div className='candidate-profile-header__aside'>
        {props.asideSection()}
      </div>
    </div>
  )
}

CandidateProfileHeader.defaultProps = {
  withAlgoliaHighlights: true,
  asideSection: () => ''
}

export default CandidateProfileHeader



