import React from 'react'
import CandidatePublicProfileViewMobile from './CandidatePublicProfileView.mobile'
import CandidatePublicProfileViewWeb from './CandidatePublicProfileView'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const CandidatePublicProfileView = props => props.isMobile ? <CandidatePublicProfileViewMobile {...props} /> : <CandidatePublicProfileViewWeb {...props} />

export default withDeviceWidthDetector(CandidatePublicProfileView)



