import React from 'react'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import HighlightedHitAttribute from 'Algolia/components/HighlightedHitAttribute'
import cx from 'classnames'
import './CandidateProfileCertificateElement.scss'

function CandidateProfileCertificateElement (props) {
  const containerClass = cx({
    'candidate-profile-certificate-element': true,
    'column col-6': !props.isMobile
  })

  const getTitle = () => props.withHighlights ? (
    <HighlightedHitAttribute
      highlightWithOwnValue
      arrayItemAttribute='certificatesName'
      arrayItemValue={props.title}
      array
      candidateId={props.candidateId}
      attribute='certificatesName'
      value={props.title}
    />
  ) : props.title

  const getIssuer = () => props.withHighlights ? (
    <HighlightedHitAttribute
      highlightWithOwnValue
      arrayItemAttribute='certificatesIssuer'
      arrayItemValue={props.issuer}
      array
      candidateId={props.candidateId}
      attribute='certificatesIssuer'
      value={props.issuer}
    />
  ) : props.issuer

  return (
    <div className={containerClass}>
      <div className='candidate-profile-certificate-element__header'>{getTitle()}</div>
      <div className='candidate-profile-certificate-element__text'>{getIssuer()}</div>
      <div className='candidate-profile-certificate-element__text'>{props.dates}</div>
    </div>
  )
}

export default withDeviceWidthDetector(CandidateProfileCertificateElement)



