import React from 'react'
import { t } from 'i18next'
import './PremiumTag.scss'

function PremiumTag () {
  return (
    <div className='premium-tag'>
      <i className='icon-im-star-circle' />
      &nbsp;
      <span>{t('common.premium')}</span>
    </div>
  )
}

export default PremiumTag



