import React from 'react'
import { mapIndexed } from 'Shared/helpers/Ramda'
import HighlightedHitAttribute from 'Algolia/components/HighlightedHitAttribute'
import './CandidateProfileTagList.scss'

function CandidateProfileTagList (props) {
  const TagElement = (tag, id) => <div className='candidate-profile-tags__tag-element' key={`${tag}-${id}`}>{tag}</div>
  const HighlightedTagElement = (tag, id) => <div className='candidate-profile-tags__tag-element' key={`${tag}-${id}`}>
    <HighlightedHitAttribute
      highlightWithOwnValue
      arrayItemAttribute='skillsName'
      arrayItemValue={tag}
      array
      candidateId={props.candidateId}
      attribute='skillsName'
      value={tag}
    />
  </div>

  const getTagElement = props.withHighlights ? HighlightedTagElement : TagElement

  const tagsList = mapIndexed(
    getTagElement
  )(props.tagsList)

  return (
    <div className='candidate-profile-tags__tags-container'>{tagsList}</div>
  )
}

export default CandidateProfileTagList



