import React from 'react'
import CandidateProfileCertificateElementConnector from 'Employer/connectors/Components/CandidateProfileCertificateElementConnector'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import { map } from 'ramda'
import cx from 'classnames'

function CandidateProfileCertificates (props) {
  const renderCertificateElement = certificate => <CandidateProfileCertificateElementConnector id={certificate.id} key={certificate.id} candidateId={props.candidateId} withHighlights={props.withHighlights} />

  const certificatesList = () => map(
    renderCertificateElement
  )(props.certificates)

  const containerClass = cx({
    'columns': !props.isMobile
  })

  return (
    <div className={containerClass}>{certificatesList()}</div>
  )
}

export default withDeviceWidthDetector(CandidateProfileCertificates)



