import React from 'react'
import { t } from 'i18next'
import './CandidatePublicProfileButtons.scss'
import { PrimaryButton, PrimaryButtonOutline } from 'Shared/components/Button'
import { Link } from 'react-router-dom'

function CandidatePublicProfileButtons (props) {
  return (
    <div className='public-profile-buttons'>
      <Link to='/' className='public-profile-buttons__button'>
        <PrimaryButton>
          {t('common.learnMoreOnOliv')}
        </PrimaryButton>
      </Link>
      <Link to='/employers' className='public-profile-buttons__button'>
        <PrimaryButtonOutline>
          {t('common.hireMe')}
        </PrimaryButtonOutline>
      </Link>
    </div>
  )
}

export default CandidatePublicProfileButtons



