import { connect } from 'react-redux'
import CandidateProfileCertificateElement from 'Employer/components/CandidateProfileDetails/CandidateProfileCertificates/components/CandidateProfileCertificateElement'
import {
  getCertificateName,
  getCertificateIssuer,
  getCertificateDate
} from 'Shared/selectors/certificates'
import { t } from 'i18next'

const mapStateToProps = (state, ownProps) => ({
  title: getCertificateName(state, ownProps.id),
  issuer: getCertificateIssuer(state, ownProps.id),
  dates: `${t('forms.yearOfCompletion.label')}: ` + getCertificateDate(state, ownProps.id)
})

export default connect(mapStateToProps)(CandidateProfileCertificateElement)



