import { connect } from 'react-redux'
import CandidateProfileExperienceElement from 'Employer/components/CandidateProfileDetails/CandidateProfileExperience/components/CandidateProfileExperienceElement'
import {
  getExperiencePosition,
  getExperienceOrganisationName,
  getExperienceDates,
  getExperienceDescription
} from 'Shared/selectors/experience'

const mapStateToProps = (state, ownProps) => ({
  title: getExperiencePosition(state, ownProps.id),
  organisationName: getExperienceOrganisationName(state, ownProps.id),
  dates: getExperienceDates(state, ownProps.id),
  description: getExperienceDescription(state, ownProps.id)
})

export default connect(mapStateToProps)(CandidateProfileExperienceElement)



