import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import CandidateProfileCertificates from 'Employer/components/CandidateProfileDetails/CandidateProfileCertificates'
import { getCandidateCertificates } from 'Shared/selectors/certificates'
import { getIdFromUrl } from 'Shared/helpers/string'

const candidateId = getIdFromUrl(window.location.pathname)

const mapStateToProps = state => ({
  certificates: getCandidateCertificates(state, candidateId),
  withHighlights: false,
  candidateId
})

export default compose(
  withRouter,
  connect(mapStateToProps)
)(CandidateProfileCertificates)



