import React, { useState } from 'react'
import { t } from 'i18next'
import { isEmpty, not, pipe } from 'ramda'
import { PreviewModalMobile } from 'Shared/components/Modal'
import { PageTopHeaderMobile } from 'Shared/components/PageTopHeader'
import Tooltip from 'Shared/components/Tooltip/Tooltip'
import './CandidateProfileNavbar.scss'
import cx from 'classnames'

function CandidateProfileNavbar (props) {
  const [availabilityModalVisibility, setAvailabilityModalVisibility] = useState(false)

  const openAvailabilityModal = () => setAvailabilityModalVisibility(true)
  const closeAvailabilityModal = () => setAvailabilityModalVisibility(false)

  const hasAvailabilitySection = pipe(
    isEmpty,
    not
  )(props.availabilitySection())

  const getTooltipText = props.isAvailable ? t('profile.availabilityTooltip.available') : t('profile.availabilityTooltip.notAvailable')

  const calendarIconClass = cx({
    'icon-im-calendar candidate-profile-navbar__availability-btn': true,
    'candidate-profile-navbar__availability-btn--notification': !props.isAvailable
  })

  const availabilityTrigger = () => (
    <div>
      <i onClick={openAvailabilityModal} className={calendarIconClass} />
      <PreviewModalMobile
        title={<span>
          {t('profile.preferences.sections.availability.label')}&nbsp;
          <Tooltip content={getTooltipText} position='top' trigger='click'>
            <i className='ion-ios-information-circle candidate-profile-navbar__availability-tooltip' />
          </Tooltip>
        </span>}
        show={availabilityModalVisibility}
        handleClose={closeAvailabilityModal}
      >
        {props.availabilitySection()}
      </PreviewModalMobile>
    </div>
  )

  return (
    <div>
      <PageTopHeaderMobile light title={' '} />
      <div className='candidate-profile-navbar__container'>
        {hasAvailabilitySection && availabilityTrigger()}
        {props.actionButtons()}
      </div>
    </div>
  )
}

CandidateProfileNavbar.defaultProps = {
  availabilitySection: () => '',
  actionButtons: () => ''
}

export default CandidateProfileNavbar



