import React from 'react'
import HighlightedHitAttribute from 'Algolia/components/HighlightedHitAttribute'
import './CandidateProfileExperienceElement.scss'
import { parseHtmlWithoutSpan } from 'Shared/helpers/editor'

function CandidateProfileExperienceElement (props) {
  const getTitle = () => props.withHighlights ? (
    <HighlightedHitAttribute
      highlightWithOwnValue
      arrayItemAttribute='experiencesPosition'
      arrayItemValue={props.title}
      array
      candidateId={props.candidateId}
      attribute='experiencesPosition'
      value={props.title}
    />
  ) : props.title

  const getOrganisationName = () => props.withHighlights ? (
    <HighlightedHitAttribute
      highlightWithOwnValue
      arrayItemAttribute='experiencesOrganisationName'
      arrayItemValue={props.organisationName}
      array
      candidateId={props.candidateId}
      attribute='experiencesOrganisationName'
      value={props.organisationName}
    />
  ) : props.organisationName

  const getDescription = () => props.withHighlights ? (
    <HighlightedHitAttribute
      highlightWithOwnValue
      arrayItemAttribute='experiencesPosition'
      arrayItemValue={props.title}
      array
      candidateId={props.candidateId}
      attribute='experiencesDescription'
      value={props.description}
    />
  ) : parseHtmlWithoutSpan(props.description)

  return (
    <div className='candidate-profile-experience-element__container'>
      <div className='candidate-profile-experience-element__header'>{getTitle()}</div>
      <div className='candidate-profile-experience-element__text'>{getOrganisationName()}</div>
      <div className='candidate-profile-experience-element__text'>{props.dates}</div>
      <div className='candidate-profile-experience-element__text'>{getDescription()}</div>
    </div>
  )
}

export default CandidateProfileExperienceElement



