import React, { Fragment } from 'react'
import { parseHtmlWithoutSpan } from 'Shared/helpers/editor'
import { pure } from 'recompose'
import * as R from 'ramda'

const HighlightOwnValue = ({ highlight, attribute, hit, apiValue, array, arrayItemValue, arrayItemAttribute }) => {
  const getArrayItemValueNumber = () => R.pipe(
    R.propOr([], arrayItemAttribute),
    R.findIndex(
      R.equals(arrayItemValue)
    ),
    R.add(1)
  )(hit)

  const valueFromAlgolia = array ? R.pipe(
    R.pathOr([], ['_highlightResult', attribute]),
    R.take(getArrayItemValueNumber()),
    R.last,
    R.propOr('', 'value')
  )(hit) : R.pipe(
    R.pathOr('', ['_highlightResult', attribute, 'value'])
  )(hit)

  const highlightPattern = /<ais-highlight-0000000000>(.*?)<\/ais-highlight-0000000000>/gi

  const stripTags = R.pipe(
    R.split('<ais-highlight-0000000000>'),
    R.last,
    R.split('</ais-highlight-0000000000>'),
    R.head
  )

  const extractHighlightedWords = R.pipe(
    R.match(highlightPattern),
    R.map(
      stripTags
    )
  )

  const wordsToHighlight = extractHighlightedWords(valueFromAlgolia)

  const highlightWords = text => words => (
    R.reduce(
      (acc, word) => R.replace(word, `<mark>${word}</mark>`, acc),
      text
    )(words)
  )

  const highlightedText = highlightWords(apiValue)(wordsToHighlight)

  return (
    <Fragment>
      {parseHtmlWithoutSpan(highlightedText)}
    </Fragment>
  )
}

export default pure(HighlightOwnValue)



