import { connect } from 'react-redux'
import CandidateProfileUniversityElement from 'Employer/components/CandidateProfileDetails/CandidateProfileEducation/components/CandidateProfileUniversityElement'
import {
  getUniversityNameById,
  getEducationDescription,
  getEducationGraduateDate,
  getEducationGradingInfo,
  getEducationNormalizedGrade,
  getUniversityLogoById
} from 'Shared/selectors/education'
import { t } from 'i18next'

const mapStateToProps = (state, ownProps) => ({
  logoUrl: getUniversityLogoById(state, ownProps.id),
  title: getUniversityNameById(state, ownProps.id),
  description: getEducationDescription(state, ownProps.id),
  graduationDate: `${t('common.graduationDate')}: ` + getEducationGraduateDate(state, ownProps.id),
  gradingInfo: getEducationGradingInfo(state, ownProps.id),
  normalizedGrade: `${t('profile.education.normalizedGrade.title')}: ` + getEducationNormalizedGrade(state, ownProps.id)
})

export default connect(mapStateToProps)(CandidateProfileUniversityElement)



