import React, { Component } from 'react'
import * as R from 'ramda'
import { parseHtmlWithoutSpan } from 'Shared/helpers/editor'

const replaceHighlightTagWithMark = R.replace(/ais-highlight-0000000000/g, 'mark')

class HighlightSnippet extends Component {
  getHitHighlightValue = () => {
    const { array, attribute, hit } = this.props
    const getArrayHighlightValues = R.pipe(
      R.pathOr([], ['_highlightResult', attribute]),
      R.map(R.propOr('', 'value')),
      R.join(', ')
    )
    const getHighlightValue = R.pathOr('', ['_highlightResult', attribute, 'value'])
    return array ? getArrayHighlightValues(hit) : getHighlightValue(hit)
  }

  getResults = () => {
    const hitHighlightedValue = this.getHitHighlightValue()
    return replaceHighlightTagWithMark(hitHighlightedValue)
  }

  render () {
    const results = this.getResults()

    return (
      <span>
        {parseHtmlWithoutSpan(results)}
      </span>
    )
  }
}

export default HighlightSnippet



