import React from 'react'
import CandidateProfileNavbarMobile from './CandidateProfileNavbar.mobile'
import CandidateProfileNavbarWeb from './CandidateProfileNavbar'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'

const CandidateProfileNavbar = props => props.isMobile ? <CandidateProfileNavbarMobile {...props} /> : <CandidateProfileNavbarWeb {...props} />

export default withDeviceWidthDetector(CandidateProfileNavbar)



