import React from 'react'
import UserAvatar from 'Shared/components/UserAvatar/UserAvatar'
import ProfilePreviewSection from 'Candidate/components/ProfilePreviewSection'
import './CandidateProfileHeader.scss'
import { getFullName, getMajor } from './CandidateProfileHeader'
import PremiumTag from 'Shared/components/PremiumTag'

function CandidateProfileHeaderMobile (props) {
  return (
    <ProfilePreviewSection>
      <div className='candidate-profile-header-mobile__container'>
        <UserAvatar details={props.details} className='candidate-profile-header-mobile__photo' />
        <div className='candidate-profile-header-mobile__heading'>
          {
            props.hasOlivPlus && <div className='candidate-profile-header-mobile__tag'><PremiumTag /></div>
          }
          {getFullName(props)}
          <div className='candidate-profile-header-mobile__text candidate-profile-header-mobile__text--primary candidate-profile-header-mobile__text--margin'>
            {getMajor(props)}
          </div>
          <div className='candidate-profile-header-mobile__text candidate-profile-header__text--capitalize candidate-profile-header-mobile__text--margin'>{props.location}</div>
        </div>
      </div>
      <div className='candidate-profile-header-mobile__contact-container'>
        {props.contactSection()}
      </div>
      <div className='candidate-profile-header-mobile__aside'>
        {props.asideSection()}
      </div>
    </ProfilePreviewSection>
  )
}

export default CandidateProfileHeaderMobile



