import React from 'react'
import CandidateProfileExperienceElementConnector from 'Employer/connectors/Components/CandidateProfileExperienceElementConnector'
import { map } from 'ramda'

function CandidateProfileExperience (props) {
  const renderExperienceElement = experience => <CandidateProfileExperienceElementConnector id={experience.id} key={experience.id} candidateId={props.candidateId} withHighlights={props.withHighlights} />

  const experiencesList = () => map(
    renderExperienceElement
  )(props.experiences)

  return (
    <div>{experiencesList()}</div>
  )
}

export default CandidateProfileExperience



