import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import ApplicationProfileAbout from 'Employer/components/CandidateProfileDetails/CandidateProfileAbout'
import { getIdFromUrl } from 'Shared/helpers/string'
import { getCandidatePitch } from 'Shared/selectors/candidates'

const candidateId = getIdFromUrl(window.location.pathname)

const mapStateToProps = (state, ownProps) => ({
  pitch: getCandidatePitch(state, candidateId)
})

export default compose(
  withRouter,
  connect(mapStateToProps)
)(ApplicationProfileAbout)



