import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import CandidatePublicProfileView from 'Shared/views/CandidatePublicProfile'
import { fetchCandidatePublicProfile } from 'Shared/ducks/candidates'
import { fetchCountriesIfNeeded } from 'Shared/ducks/resources'
import { getIdFromUrl } from 'Shared/helpers/string'
import { hasDatabaseSearchAvailable } from 'Shared/selectors/membership'
import {
  getCandidateFirstName,
  getCandidateLastName,
  getCandidateEducationLevel,
  getCandidateLastEducationName, hasCandidateVideoCv
} from 'Shared/selectors/candidates'
import { isEmployer } from 'Shared/selectors/user'
import { hasCandidateAnyCertificates } from 'Shared/selectors/certificates'
import { hasCandidateAnyExperience, hasCandidateAnyVolunteering } from 'Shared/selectors/experience'
import { hasCandidateAnySkills } from 'Shared/selectors/skills'
import { hasCandidateAnyLanguages } from 'Shared/selectors/languages'
import { hasCandidateAnyInterests } from 'Shared/selectors/interests'

const userId = getIdFromUrl(window.location.pathname)

const mapStateToProps = state => ({
  firstName: getCandidateFirstName(state, userId),
  lastName: getCandidateLastName(state, userId),
  educationLevel: getCandidateEducationLevel(state, userId),
  userId,
  hasDatabaseSearchAvailable: hasDatabaseSearchAvailable(state),
  lastEducationName: getCandidateLastEducationName(state, userId),
  isEmployer: isEmployer(state),
  hasCandidateAnyCertificates: hasCandidateAnyCertificates(state, userId),
  hasCandidateAnyExperience: hasCandidateAnyExperience(state, userId),
  hasCandidateAnyVolunteering: hasCandidateAnyVolunteering(state, userId),
  hasCandidateAnySkills: hasCandidateAnySkills(state),
  hasCandidateAnyLanguages: hasCandidateAnyLanguages(state, userId),
  hasCandidateVideoCv: hasCandidateVideoCv(state, userId),
  hasCandidateAnyInterests: hasCandidateAnyInterests(state)
})

const mapDispatchToProps = dispatch => ({
  fetchCandidatePublicProfile: () => dispatch(fetchCandidatePublicProfile(userId)),
  fetchCountries: () => dispatch(fetchCountriesIfNeeded())
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(CandidatePublicProfileView))



