import React from 'react'
import Highlight from 'Algolia/components/Highlight'
import HighlightOwnValue from 'Algolia/components/HighlightOwnValue'
import { connectHits } from 'react-instantsearch-dom'
import { parseHighlightedCandidateHit } from 'Algolia/helpers/highlights'
import { parseHtmlWithoutSpan } from 'Shared/helpers/editor'
import * as R from 'ramda'

const HighlightedHitAttribute = ({ hits, attribute, value, candidateId, array, className, arrayItemValue, highlightWithOwnValue, arrayItemAttribute }) => {
  const hit = R.find(
    R.propEq('id', candidateId)
  )(hits)

  const parsedHit = hit ? parseHighlightedCandidateHit(hit) : {}

  const getHighlightedText = highlightWithOwnValue
    ? <HighlightOwnValue attribute={attribute} hit={parsedHit} apiValue={value} array={array} arrayItemValue={arrayItemValue} arrayItemAttribute={arrayItemAttribute} />
    : <Highlight attribute={attribute} hit={parsedHit} value={value} array={array} arrayItemValue={arrayItemValue} className={className} />

  const getSimpleValue = typeof value === 'object' ? value.map(e => e) : parseHtmlWithoutSpan(value)

  return hit ? getHighlightedText : getSimpleValue
}

export default connectHits(HighlightedHitAttribute)



