import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter } from 'react-router-dom'
import CandidateProfileEducation from 'Employer/components/CandidateProfileDetails/CandidateProfileEducation'
import { isCandidateHighSchoolLevelEducation } from 'Shared/selectors/candidates'
import { getCandidateHighSchoolEducation } from 'Shared/selectors/highSchoolLevelEducation'
import { getCandidateEducation } from 'Shared/selectors/education'
import { fetchEducationResources } from 'Shared/ducks/resources'
import { getIdFromUrl } from 'Shared/helpers/string'

const candidateId = getIdFromUrl(window.location.pathname)

const mapStateToProps = (state, ownProps) => ({
  isHighSchoolLevelEducation: isCandidateHighSchoolLevelEducation(state, candidateId),
  universities: getCandidateEducation(candidateId)(state),
  highSchools: getCandidateHighSchoolEducation(state, candidateId),
  withHighlights: false,
  candidateId
})

const mapDispatchToProps = dispatch => ({
  fetchEducationResources: () => dispatch(fetchEducationResources())
})

export default compose(
  withRouter,
  connect(mapStateToProps, mapDispatchToProps)
)(CandidateProfileEducation)



