import React from 'react'
import withDeviceWidthDetector from 'Shared/hoc/withDeviceWidthDetector/withDeviceWidthDetector'
import cx from 'classnames'
import './CandidateProfileHighSchoolElement.scss'

function CandidateProfileHighSchoolElement (props) {
  const containerClass = cx({
    'candidate-profile-high-school-element': true,
    'column col-6': !props.isMobile
  })

  return (
    <div className={containerClass}>
      <div className='candidate-profile-high-school-element__header'>{props.title}</div>
      <div className='candidate-profile-high-school-element__text'>{props.description}</div>
      <div className='candidate-profile-high-school-element__text candidate-profile-high-school-element__text--mobile-grey'>{props.graduationDate}</div>
    </div>
  )
}

export default withDeviceWidthDetector(CandidateProfileHighSchoolElement)



