import { connect } from 'react-redux'
import CandidateProfileHighSchoolElement from 'Employer/components/CandidateProfileDetails/CandidateProfileEducation/components/CandidateProfileHighSchoolElement/CandidateProfileHighSchoolElement'
import { getHighSchoolName, getEducationSystem, getEducationGraduationDate } from 'Shared/selectors/highSchoolLevelEducation'
import { t } from 'i18next'

const mapStateToProps = (state, ownProps) => ({
  title: getHighSchoolName(state, ownProps.id),
  description: `${t('forms.educationSystem.label')}: ` + getEducationSystem(state, ownProps.id),
  graduationDate: `${t('common.graduationDate')}: ` + getEducationGraduationDate(state, ownProps.id)
})

export default connect(mapStateToProps)(CandidateProfileHighSchoolElement)



